.bg-ban-bel{
    background: #f5f5ed;
    padding: 25px;
    margin-bottom: 30px;
    min-height: 260px;
    display: flex;
    align-items: center;
}
.bg-ban-bel .content span{
    color: var(--primry-clr);
}
.bg-ban-bel .dcnt-text{
    font-size: 22px;
    color: black;
    margin-bottom: 0;
    line-height: 1.22;
}
.bg-ban-bel .content .thm-btn{
    background-color: white;
    color: black;
    margin-top: 15px;
}
.bg-ban-bel.active{
    background-color: var(--primry-clr);
}
.bg-ban-bel.active .content span{
    color: white;
}
.bg-ban-bel.bg-ban-bel.active .dcnt-text{
    color: white;
}